.font-body, 
.tooltip.trackerStep { 
  font-size: $font-normal; 
}

.no-line-height { line-height: 1.2rem!important; }

.font-h4 { 
  font-size: $font-h4;
  line-height: 1.2rem;
}

.font-h4 {
  line-height: 2.2rem;
  &.lh-default {
    line-height: 2.5rem;
  }
}

// .font-h4.font-h4-title {
//   line-height: 1.8rem;
// }

.font-h3 {  
  font-size: $font-h3;
  line-height: 1.2rem;
  &.font-h3--content {
    line-height: 2.4rem;
  }
}

.font-h2 {   
  font-size: $font-h2;
  line-height: 1.2rem;
  &.li-default {
    line-height: 4rem;
  }
  &.lh-head {
    line-height: 2.5rem;
  }
}

.font-h1 {  
  font-size: $font-h1;
  line-height: 4.8rem;
  font-weight: 700;
}

.font-weight-medium { font-weight: 600; }
.font-weight-heavy { font-weight: 800; }