/*modules*/ /*components*/ /*_tables.scss*/ //CHANGE CLASSES USED IN VIEWS
%border-style { border-style: solid; border-color: #f5f5f5;}
%border { border-width: 1px 1px 1px 1px; }
%border-nr { border-width: 1px 0 1px 1px; }
%border-nb { border-width: 1px 1px 0 1px; }
%border-nt { border-width: 0 1px 1px 1px; }
%no-border-er { border-width: 0 1px 0 0; }
%border-bottom { 
  border-style: solid;
  border-color: $border;
  border-width: 0 0 1px 0; 
}
%border-top { 
  border-style: solid;
  border-color: $border;
  border-width: 1px 0 0 0; 
}
%no-border { border-width: 0 0 0 0!important; border: 0; }
%border { border: 1px solid $border; }

.t-data {
  width: 100%;
  font-family: $font-body;
  font-size: $font-normal;
  width: 100%;
  background: $white;
  .t-data__td:not(.ellipsis-remove) { 
    z-index: 0;
  }
}

.t-data--border {
  @extend %border;
}

.t-data--no-border {
  @extend %no-border;
}

.t-data__row---no-border {
  @extend %no-border;
}

.t-data_td--b-border {
  @extend %border-bottom;
}

.t-data_td--t-border {
  @extend %border-top;
}

.t-data__row---border {
  @extend %border;
}

.t-data__row--select {
  &:hover {
    cursor: pointer;
  }
  &:hover:not(.t-data__head), &.active:not(.t-data__head) {
    background: #f5f5f5;
  }  
}

.t-data .t-data__th {
  vertical-align: middle;
  text-align: left;
  @include padding-mixin(1.5rem 3rem);
}

.t-data .t-data__td {
  vertical-align: middle;
  text-align: left;
  @include padding-mixin(1rem 3rem);
}


.table .t-data__td {
  // height: 50px;
}

// .t-data__td--lg-top {}

.t-data .t-data__a {
  &:hover {
    color: $black;
    text-decoration: none; 
  }
}

.t-data__td--ellipsis { //td-ellipsis
  max-width: 3rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; 
  &:hover {
    text-overflow: inherit; overflow: visible;
  }
  &:hover span.td-ellipsis {
    background: white; 
    z-index: 999; 
    padding: .2rem;
    margin-left: -.2rem; 
    position: relative; 
    color: #212121;
  }
}

// .t-data__td-panel-p-l { padding: 1.3rem 1rem 1.2rem 3rem; } 

.slick-container .slick-header { 
  padding: 1.3rem 1rem; 
  line-height: 1.3rem; 
  vertical-align: top; 
}

table.t-border-space { border-spacing: 0rem 0.75rem; border-collapse: separate; }

.t-list tbody tr {
  -webkit-transition: background .2s ease-in-out;
  -o-transition: background .2s ease-in-out;
  transition: background .2s ease-in-out;
}

.scrollable-area { /*modules*/ /*components*/ /*_tables.scss*  ADD CLASS INSTAD OF SELECTING WITH ID*/ 
  margin: auto;
  width: 100%;
  overflow-y: hidden;  /*<!-- here is what is important-->*/
  overflow-x: hidden;
  border-bottom: 50px solid $white;
  border-radius: 10px;
  -webkit-box-shadow: 0px 7px 5px 0px rgba(0,0,0,0.1);
  -moz-box-shadow: 0px 7px 5px 0px rgba(0,0,0,0.1);
  box-shadow: 0px 7px 5px 0px rgba(0,0,0,0.1);

  ::-webkit-scrollbar {
    width: 15px;
  }
  &.scrollable-area--no-shadow {
    -webkit-box-shadow: 0 0 0 0 rgba(0,0,0,0);
    -moz-box-shadow: 0 0 0 0 rgba(0,0,0,0);
    box-shadow: 0 0 0 0 rgba(0,0,0,0);
  }

  /* Track */
  // ::-webkit-scrollbar-track {
  //   box-shadow: inset 0 0 5px;
  //   border-radius: 10px;
  //   max-height: 310px;
  // }
   
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: $grey; 
    border-radius: 10px;
    height: 30px;
  }

  /* Handle on hover */
  // ::-webkit-scrollbar-thumb:hover {
  //   opacity: 0.7 
  // }  
}


