.table-scroll thead {
    display: table;
    width: 100%;
    table-layout: fixed;
}

.table-scroll {
    &>tbody {
      overflow-y: auto;
      display: block;
      width: 100%;
      table-layout: fixed;
    }
    &>.portfolio-table, &>.transaction-table, &>.renewalhistory-table  {
        max-height: 280px;
    }
    &>.dashboard-table {
        max-height: 190px;
    }    
}

.table-scroll tr {
    display: table;
    table-layout: fixed;
    width: 100%;
}


